import {
  FilterButton,
  PostFilterContainer,
} from "../../styles/Posts/PostFilterStyle";
import { useNavigate } from "react-router-dom";
import { URI } from "../../utils/constants";

function PostFilter() {
  const navigate = useNavigate();

  return (
    <PostFilterContainer>
      <FilterButton onClick={() => navigate("/posts")}></FilterButton>
      <FilterButton onClick={() => navigate(`/posts?${URI.SORT_LIKE}`)}>
        
      </FilterButton>
      <FilterButton onClick={() => navigate(`/posts?${URI.SORT_TITLE}`)}>
        
      </FilterButton>
      <FilterButton onClick={() => navigate(`/posts?${URI.SORT_AUTHOR}`)}>
        
      </FilterButton>
    </PostFilterContainer>
  );
}

export default PostFilter;
